.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.hedaer-div {
  box-sizing: border-box;
  width: 100%;
  display: flex;
  /* justify-content: center; */
  padding: 1em;
  margin-bottom: 2em;
  background-color: rgb(192, 45, 26);
  color: #fff;
}

.title{
  justify-content: left;
  color: #fff;
  text-align: left;
}
.selectCoutry{
  justify-content: right;
  text-align: right;
  width: 75%;
}
.selectBox{
  width: 20%;
}
th{
  text-align: center;
}
.mapping_table tbody {
  display:block;
  height:300px;
  overflow-y:scroll;
  }
  .mapping_table thead{
  display:table;
  width:100%;
  table-layout:fixed;
  }
  .mapping_table tbody tr {
  display:table;
  width:100%;
  table-layout:fixed;
  }
